#product-cart {
  background-color: white;
  border: 1px solid $border-color;
  box-shadow: 0 0 10px $border-color;

  .cart-header {
    border-bottom: 1px solid $border-color;
    padding: 20px;
  }

  .cart-body {
    padding: 20px;
  }

  .product-name {
    font-size: 20px;
    @include media-breakpoint-down(lg) {
      font-size: 20px;
    }
    @include media-breakpoint-down(md) {
      font-size: 20px;
    }
    @include media-breakpoint-down(sm) {
      font-size: 18px;
    }
    margin-bottom: 10px;
  }

  .product-code {
    text-transform: none;
    font-weight: bold;

    span {
      color: $main-color;
    }
  }

  .product-price {
    color: $sale-color;
    font-size: 32px;
    font-weight: bold;
	margin-bottom: 10px;

    sup {
      font-weight: bold;
    }
	
	.contact {
		font-size: 24px;
		color: $sale-color;
	  	font-weight: bold;
	  	a {
	  		color: $sale-color;
	  		font-weight: bold;
	  	}
  	}

    @include media-breakpoint-down(lg) {
      font-size: 32px;
    }
    @include media-breakpoint-down(md) {
      font-size: 32px;
    }
    @include media-breakpoint-down(sm) {
      font-size: 30px;
    }
  }

  .product-discount {
    margin: 0 0 10px;

    .percentage-discount, .original-price {
      display: inline-block;
    }

    .percentage-discount {
      background-color: $sale-color;
      color: white;
      font-weight: 600;
      @include border-radius(3px);
      padding: 6px 10px;
      margin-right: 10px;
    }

    .original-price {
      color: $gray-color;
      text-decoration: line-through;
      @include media-breakpoint-down(xs) {
        display: none;
      }
    }
  }

  .product-action {
    @include make-row();

    > div {
      @include make-col-ready();

      &:first-child {
        @include media-breakpoint-up(md) {
          @include make-col(5);
        }
		@include media-breakpoint-down(xs) {
			padding: 0 5px 0 10px!important;
		}
      }

      &:last-child {
        @include media-breakpoint-up(md) {
          @include make-col(7);
        }
		@include media-breakpoint-down(xs) {
			padding: 0 10px 0 5px!important;
		}
      }
	  
	  @include media-breakpoint-down(xs) {
	  	/*padding: 0 5px!important;*/
	  }
    }

    button {
      width: 100%;
    }
  }
}

.product-page-wrapper {
  .product-cart-selected {
    opacity: 0;
    @include transition(all, .25s);

    &.is-affixed {
      opacity: 1;
    }

    .inner-wrapper-sticky {
      @include media-breakpoint-down(md) {
        top: auto !important;
        bottom: 0;
        z-index: 99;
        transform: translate3d(0, 0, 0) !important;
      }
    }

    .cart-body {
      @include media-breakpoint-down(xs) {
        padding: 10px !important;
        background: white;
        box-shadow: 0px 3px 10px rgba(#444, 0.15);
        margin: 0 -5vw;
      }
    }

    .cart-header, .product-price, .product-discount {
      @include media-breakpoint-down(xs) {
        display: none;
      }
    }

    .product-action {
      > div {
        @include media-breakpoint-down(xs) {
          @include make-col(6);
        }
      }
    }
  }
}
