#product-feature {
  position: relative;
  background-color: white;
  border: 1px solid $border-color;
  box-shadow: 0 0 10px $border-color;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 5px;
    background-color: $main-color;
  }
  .feature-header {
    border-bottom: 1px solid $border-color;
    padding: 25px 30px;
  }
  .feature-label {
    font-size: 16px;
    font-weight: bold;
  }
  .feature-body {
    padding-bottom: 15px;
	
	@include media-breakpoint-down(xs) {
		img {
			width: 100%;
			margin: 10px auto;
		}
	}
  }
  ul.specification-list {
    border-bottom: 1px solid $border-color;
    padding: 25px 30px;
    > li {
      width: 100%;
      display: table;
      margin-bottom: 10px;
      &:last-of-type {
        margin-bottom: 0;
      }
      > label, > span {
        display: table-cell;
        vertical-align: top;
      }
      > label {
        width: 40%;
        /*font-weight: bold;*/
		cursor: none;
      }
      > span {
        width: 60%;
        line-height: 24px;
        padding-left: 20px;
      }
    }
  }
  .feature-text {
    padding: 25px 30px;
	font-family: $base-font!important;
	font-size: $font-size;
	
    p {
      margin-bottom: 15px;
    }
	
	span {
		color: #333!important;
	}
	
	a {
		color: #962d91!important;
		
		&:hover {
			text-decoration: underline;
		}
	}
	
	h1, h2, h3, h4, h5, h6 {
		/*font-size: 1.5rem;*/
		margin-bottom: .5rem;
		font-weight: 500;
		line-height: 1.5;
	}
	h2, h3 {
		font-size: 1.5rem;
	}
	
	iframe {
		width: 100%!important;
		margin: 5px auto 20px;
		height: calc(100vw - 145vh)!important;
		min-height: 300px!important;
	}
	
	ul, ol {
		margin: 10px;
		
		li {
			margin: 5px 15px;
		}
	}
	
	table {
		border: 0;
		margin: 10px 0 20px 0;
		padding: 10px!important;
		width: 100%!important;
		
		tbody, colgroup {
			/*border: 1px solid #333!important;*/
		}
		tr {
			/*border: 1px solid #333!important;*/
		}
		td, th {
			padding: 10px!important;
			border: 1px solid #333!important;
			width: auto;
			
			&:first-child {
			width: 50%;
		}
		}
	}
  }
  .feature-item {
    padding: 15px 30px;
    .row {
      @include align-items();
      @include justify-content();
    }
    .row > div {
      @include align-items();
      @include justify-content();
    }
  }
}