.page-breadcrumb {
  margin-bottom: 20px;
  @include media-breakpoint-down(xs) {
  	  margin-bottom: 10px;
  	}
}

.breadcrumb {
  background-color: transparent;
  @include border-radius(0);
  margin: 0;
  padding: 0;
}

.breadcrumb-item {
  +.breadcrumb-item {
    padding-left: 1.5rem;
    &:before {
      content: '\f0da';
      font-family: FontAwesome;
      color: $text-color;
      padding-right: 1.5rem;
    }
  }
}