#profile-order-history {
  background-color: white;
  border: 1px solid $border-color;
  @include border-radius(5px);
  box-shadow: 0 0 10px $border-color;
  padding: 20px 30px;
  margin-bottom: 20px;
  .order-history-label {
    i, span {
      display: inline-block;
      vertical-align: middle;
    }
    i {
      color: $main-color;
      font-size: 18px;
    }
    span {
      font-size: 16px;
      text-transform: uppercase;
      margin-left: 15px;
    }
  }
}