#product-slider {
  padding: 20px;
  .product-image {
    position: relative;
    width: 100%;
    height: 380px;
    img {
      max-height: 100%;
      max-width: 100%;
      width: auto;
      height: auto;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
    }
  }
  .product-thumbnail {
    position: relative;
    width: 100%;
    border: 1px solid $border-color;
    overflow: hidden;
    padding: 5px;
    &:before {
      content: "";
      display: block;
      padding-bottom: 68%;
    }
    div {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      text-align: center;
      background: transparent no-repeat scroll center center / 90% auto;
    }
  }

  .slider-nav {
    margin-top: 20px;
    .slick-slide {
      margin: 0 10px;
    }
    .slick-list {
      margin: 0 -10px;
    }
  }
  
  .slick-prev:before, .slick-next:before {
  	color: #ccc;
  }
}