#profile-shipping-address {
  border: 1px solid $border-color;
  @include border-radius(5px);
  margin-bottom: 10px;
  .shipping-address-header, .shipping-address-body {
    padding: 20px 30px;
  }
  .shipping-address-header {
    position: relative;
    border-bottom: 1px solid $border-color;
  }
  .shipping-address-body {
    @include media-breakpoint-up(md) {
      min-height: 146px;
    }
  }
  .shipping-address-default {
    position: absolute;
    top: 50%;
    right: 20px;
    @include transform(translate(0, -50%));
    display: block;
    background-color: #048002;
    color: white;
    font-size: 12px;
    font-weight: 600;
    text-transform: uppercase;
    border: 1px solid #048002;
    @include border-radius(5px);
    padding: 5px 10px;
  }
  ul.shipping-address-list {
    > li {
      display: block;
      margin-bottom: 10px;
      &:last-child {
        margin: 0;
      }
      > i, > span {
        display: inline-block;
        vertical-align: top;
      }
      > span {
        max-width: 280px;
        margin-left: 20px;
        word-break: break-word;
        &.user-name {
          font-weight: bold;
        }
      }
    }
  }
}