#video-list {
  margin-bottom: 20px;
  .video-box {
    background-color: white;
    border: 1px solid $border-color;
    @include border-radius(5px);
    @include box-shadow(0, 5px, 10px, $border-color);
  }
  .video-item {
    border-bottom: 1px solid $border-color;
    padding: 20px;
    &:hover, &:focus {
      .video-image div {
        @include opacity(0.9);
        @include transform(scale3d(1.1, 1.1, 1));
      }
    }
    .video-image {
      position: relative;
      width: 100%;
      overflow: hidden;
      @include border-radius(5px);
      &:before {
        content: "";
        display: block;
        padding-bottom: 68%;
      }
      div {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        text-align: center;
        background: no-repeat scroll center center / cover;
        @include transition(all, 0.5s, ease);
      }
    }
    .video-text {
      padding-top: 20px;
	  
      .video-title {
        line-height: 22px;
        font-weight: bold;
        margin-bottom: 10px;
		/*height: 72px;*/
		display: -webkit-box;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;
		overflow: hidden;
      }
      .video-view {
        color: #999;
        font-size: 12px;
        font-style: italic;
        margin-bottom: 10px;
      }
    }
  }
  .view-more {
    display: block;
    text-transform: uppercase;
    text-align: right;
    padding: 10px 20px;
    &:hover {
      color: $main-color;
    }
  }
}