#news-list {
  .news-item {
    position: relative;
    background-color: white;
    border: 1px solid $border-color;
    @include border-radius(5px);
    @include box-shadow(0, 5px, 10px, $border-color);
    overflow: hidden;
    margin-bottom: 20px;
    &:hover, &:focus {
      @include box-shadow(0, 0, 20px, rgba(0,0,0,0.2));
      .news-image div {
        @include opacity(0.9);
        @include transform(scale3d(1.1, 1.1, 1));
      }
    }
    .news-image {
      position: relative;
      width: 100%;
      overflow: hidden;
      &:before {
        content: "";
        display: block;
        padding-bottom: 72%;
      }
      div {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        text-align: center;
        background: no-repeat scroll center center / cover;
        @include transition(all, 0.5s, ease);
      }
    }
    .news-text {
      background-color: white;
      padding: 20px;
	  
      .news-title {
        /*height: 44px;*/
        font-weight: bold;
        line-height: 21px;
        overflow: hidden;
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
        margin-bottom: 10px;
      }
      .news-datetime {
        color: #999;
        font-size: 12px;
        font-style: italic;
        margin-bottom: 10px;
      }
      .news-desc {
        height: 70px;
        line-height: 22px;
        overflow: hidden;
		display: -webkit-box;
		-webkit-line-clamp: 3;
		-webkit-box-orient: vertical;
      }
    }
  }
  .view-more {
    text-align: center;
    margin-top: 40px;
    a, button {
      width: 300px;
    }
  }
}