.menu-toggle {
  position: absolute;
  left: 15px;
  top: 12px;
  cursor: pointer;
  float: right;
  font-size: 0.875rem;
  transition: all 0.3s ease-in-out;

  .menu-toggle-grippy {
    border-bottom: 2px solid #fff;
    border-top: 2px solid #fff;
    float: left;
    line-height: 0;
    margin-right: 1em;
    padding: 5px 0;
    text-indent: -999em;
    width: 25px;

    &:after {
      background: #fff;
      content: "";
      display: block;
      height: 2px;
    }
  }
}

.hidden-mobile {
  position: fixed;
  top: 40px;
  z-index: 99;
  width: 100%;
  /*background-color: rgba(black, .75);*/
  @include calc(height, '100vh - 40px');
  
  .hidden-panel-content {
  	background-color: $bg-color;
  	width: 85%;
  	@include calc(height, '100vh - 40px');
  }

  .product-navigation-action {
    position: relative;
    display: block;
    width: 100%;
    padding: 12px 10px 12px 65px;
    text-transform: uppercase;
    font-weight: 700;
    color: white;
    //background-color: #17294d;
    background-color: #999;

    i {
      position: absolute;
      left: 30px;
      font-size: 18px;
      top: 50%;
      @include translate(0, -50%);
    }
  }

  .hidden-panel-content-menu {
    position: relative;
    display: block;
    width: 100%;

    li {
      display: block;

      &:last-child {
        a {
          border-bottom: 0;
        }
      }

      a {
        position: relative;
        display: block;
        width: 100%;
        text-transform: uppercase;
        padding: 12px 10px 12px 55px;
        border-bottom: 1px solid #d6d6d6;
        @include transition(all, .25s);

        i {
          position: absolute;
          color: #333;
          left: 20px;
          font-size: 24px;
          top: 50%;
          @include transition(all, .25s);
          @include translate(0, -50%);
        }

        &:hover, &.active {
          //color: $main-color;
          color: $main-color;
          background: $secondary-color;

          i {
            //color: $main-color;
            color: $main-color;
          }
        }
      }
    }
  }
  
  .hidden-bg {
  	background-color: rgba(0,0,0,0.75);
  	width: 15vw;
  	height: 100%;
  	position: absolute;
  	left: 85vw;
  	top: 0;
  	z-index: 19;
  }
}
