.home-new-product-tab {
  display: block;
  position: relative;
  width: 100%;
  margin-bottom: 5px;
  
  .view-all {
  	top: 13px;
  }

  .page-tab-title {
    /*display: block;
    width: 100%;*/
	
	position: relative;
	font-size: 22px;
	text-transform: uppercase;
	padding: 0;
	margin-bottom: 10px;
	margin-top: 5px;
    @include media-breakpoint-down(md) {
      font-size: 22px;
    }
    @include media-breakpoint-down(sm) {
      font-size: 20px;
    }

    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      border-left: 8px solid $main-color;
    }

    .nav-item {
      display: inline-block;
      position: relative;
      padding: 0 30px 0 25px;
      color: #999999;
	  @include media-breakpoint-down(xs) {
	    padding: 5px 30px;
	  }

      &:before {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        border-right: 8px solid $main-color;
		@include media-breakpoint-down(xs) {
		  content: '';
		  border: 0px;
		}
      }

      &:last-child {
        &:before {
          display: none;
        }
      }

      &.active {
        color: $text-color;
      }

      .nav-link {
        padding: 0;
      }
    }
  }

  .product-item {
    &:hover {
      @include box-shadow(0, 0, 20px, rgba(0, 0, 0, 0.2));

      .product-hover {
        display: block;
		@include media-breakpoint-down(xs) {
		  display: none;
		}
      }
    }

    .product-hover {
      display: none;
      position: absolute;
      left: 0;
      right: 0;
      bottom: -3px;
      background: white;
      @include border-radii(0, 0, 5px, 5px);
      @include box-shadow(0, 10px, 25px, rgba(0, 0, 0, 0.1));
      overflow: hidden;
      z-index: 3;
	  
	  &.no-discount {
	  	/*bottom: -5px;*/
	  	display: none;
	  }

      .product-promotion {
        background-color: #fce9e9;
        border: 2px dotted $sale-color;
        padding: 6px 5px;
        margin: 0 10px 10px 10px;

        > i, > span {
          display: inline-block;
          vertical-align: middle;
        }

        > i {
          width: 16px;
          height: 16px;
          background: url("../img/icon/sale-label.png") no-repeat center center / 100% auto;
		  display: none;
        }

        > span {
          color: $sale-color;
          font-size: 11px;
          margin-left: 0px;
		  overflow: hidden;
		  display: -webkit-box;
		  -webkit-line-clamp: 2;
		  -webkit-box-orient: vertical;
        }
      }

      .product-add-to-cart {
        display: none;
        background-color: #e53935;
        text-align: center;
        padding: 8px 0;

        > i, > span {
          color: white;
          display: inline-block;
          vertical-align: middle;
        }

        > i {
          font-size: 20px;
          margin-right: 10px;
        }

        > span {
          font-weight: bold;
          text-transform: uppercase;
        }
      }
    }
  }
}
