#most-viewed-articles-list {
  margin-bottom: 20px;
  .news-box {
    background-color: white;
    border: 1px solid $border-color;
    @include border-radius(5px);
    @include box-shadow(0, 5px, 10px, $border-color);
  }
  .news-list-wrapper {
    .news-item:last-child {
      border: none;
    }
  }
  .news-item {
    border-bottom: 1px solid $border-color;
    padding: 20px;
    &:hover, &:focus {
      .news-image div {
        @include opacity(0.9);
        @include transform(scale3d(1.1, 1.1, 1));
      }
    }
    .news-image {
      position: relative;
      width: 100%;
      overflow: hidden;
      @include border-radius(5px);
      &:before {
        content: "";
        display: block;
        padding-bottom: 68%;
      }
      div {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        text-align: center;
        background: no-repeat scroll center center / cover;
        @include transition(all, 0.5s, ease);
      }
    }
    .news-text {
	  /*height: 100px;*/
	  
      .news-title {
        line-height: 22px;
        font-weight: bold;
        margin-bottom: 10px;
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
		/*height: 44px;*/
		overflow: hidden;
      }
      .news-datetime {
        color: #999;
        font-size: 12px;
        font-style: italic;
        margin-bottom: 10px;
      }
    }
  }
}