$color1: #fff;
$color2: #962d91;
$color3: #999999;

.radio {
  input[type="radio"] {
    position: absolute;
    opacity: 0;
    + .radio-label {
      &:before {
        content: '';
        background: $color1;
        border-radius: 100%;
        border: 1px solid $border-color;
        display: inline-block;
        width: 16px;
        height: 16px;
        position: relative;
        top: 3px;
        margin-right: 15px;
        vertical-align: top;
        cursor: pointer;
        text-align: center;
        transition: all 250ms ease;
      }
    }
    &[checked="checked"] {
      + .radio-label {
        &:before {
          background-color: $color2;
          box-shadow: inset 0 0 0 3px $color1;
        }
      }
    }
    &:focus {
      + .radio-label {
        &:before {
          outline: none;
          border-color: $color2;
        }
      }
    }
    &:disabled {
      + .radio-label {
        &:before {
          box-shadow: inset 0 0 0 4px $color1;
          border-color: $border-color;
          background: darken($color1, 25%);
        }
      }
    }
    + .radio-label {
      &:empty {
        &:before {
          margin-right: 0;
        }
      }
    }
  }
}

.checkbox {
  input[type="checkbox"] {
    position: absolute;
    opacity: 0;
    + .checkbox-label {
      &:before {
        content: '';
        background: $color1;
        border: 1px solid $border-color;
        display: inline-block;
        width: 16px;
        height: 16px;
        position: relative;
        top: 3px;
        margin-right: 15px;
        vertical-align: top;
        cursor: pointer;
        text-align: center;
        transition: all 250ms ease;
      }
    }
    &:checked {
      + .checkbox-label {
        &:before {
          background-color: $color2;
          box-shadow: inset 0 0 0 3px $color1;
        }
      }
    }
    &:focus {
      + .checkbox-label {
        &:before {
          outline: none;
          border-color: $color2;
        }
      }
    }
    + .checkbox-label {
      &:empty {
        &:before {
          margin-right: 0;
        }
      }
    }
  }
}

.tick-checkbox {
  line-height: 30px;
  input[type="checkbox"] {
    position: absolute; // take it out of document flow
    opacity: 0; // hide it

    & + label {
      position: relative;
      cursor: pointer;
      padding: 0;
      color: #048002;
      text-transform: uppercase;
    }

    // Box.
    & + label:before {
      content: '';
      background: $color1;
      border: 1px solid $border-color;
      margin-right: 10px;
      margin-top: 2px;
      display: inline-block;
      vertical-align: text-top;
      width: 16px;
      height: 16px;
      background: white;
    }

    // Box hover
    &:hover + label:before {
      background: #048002;
    }

    // Box focus
    //&:focus + label:before {
    //  box-shadow: 0 0 0 3px rgba(0, 0, 0, 0.12);
    //}

    // Box checked
    &:checked + label:before {
      background: #048002;
    }

    // Disabled state label.
    &:disabled + label {
      color: #b8b8b8;
      cursor: auto;
    }

    // Disabled box.
    &:disabled + label:before {
      box-shadow: none;
      background: #ddd;
    }

    // Checkmark. Could be replaced with an image
    &:checked + label:after {
      content: '';
      position: absolute;
      left: 3px;
      top: 13px;
      background: white;
      width: 2px;
      height: 2px;
      box-shadow:
              2px 0 0 white,
              4px 0 0 white,
              4px -2px 0 white,
              4px -4px 0 white,
              4px -6px 0 white,
              4px -8px 0 white;
      transform: rotate(45deg);
    }
  }
}
