#profile-address {
  .address-item {
    min-height: 185px;
    border: 1px solid $border-color;
    @include border-radius(5px);
    margin-bottom: 20px;
  }
  .address-body {
    padding: 20px 30px;
  }
  .address-footer {
    text-align: right;
    border-top: 1px solid $border-color;
    padding: 10px 20px;
    button {
      display: inline-block;
      margin-left: 10px;
    }
    .checkbox {
      label {
        text-transform: uppercase;
      }
    }
  }
  ul.address-list {
    > li {
      display: block;
      margin-bottom: 10px;
      &:last-child {
        margin: 0;
      }
      > i, > span {
        display: inline-block;
        vertical-align: top;
      }
      > span {
        max-width: 280px;
        margin-left: 20px;
        word-break: break-word;
        &.user-name {
          font-weight: bold;
        }
      }
    }
  }
  .add-address-item {
    @include flexbox();
    @include align-items();
    @include justify-content();
    span {
      display: inline-block;
      color: $main-color;
    }
  }
}