#profile-order-detail {
  display: block;
  width: 100%;
  position: relative;

  .order-detail {
    display: block;
    width: 100%;
    margin-bottom: 15px;
    border: 1px solid $border-color;
    @include border-radius(6px);

    .section-title {
      font-size: 20px;
      text-transform: uppercase;
      border-bottom: 1px solid $border-color;
      padding: 15px;
      position: relative;
	  
	  span {
	  	color: $main-color;
	  	font-weight: bold;
	  }

      .order-status {
        position: absolute;
        right: 15px;
        top: 15px;
        font-size: 12px;
        background-color: #1c7430;
        color: white;
        padding: 6px 15px;
        @include border-radius(6px);
      }
    }

    .order-detail-body {
      padding: 15px;
      position: relative;
	  
	  .row {
	  	margin: 10px 0;
	  }

      p {
        strong {
          font-weight: bold;

        }

        .main-color {
          font-weight: bold;
          color: $main-color;
        }
      }

      &.border-order {
        border-bottom: 1px solid $border-color;
      }

      .order-detail-image {
        display: block;
        width: 100%;
        padding: 5px;
        height: 100px;
        position: relative;
        border: 1px solid $border-color;
        @include border-radius(6px);

        @include media-breakpoint-down(sm) {
          height: 95px;
        }

        figure {
          position: relative;
          width: 100%;
          height: 100%;

          img {
            max-height: 100%;
            max-width: 100%;
            width: auto;
            height: auto;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            margin: auto;
            @include transition(transform, 0.5s, linear);
          }
        }
      }

      .order-detail-info {
        display: block;
        width: 100%;
        position: relative;

        h6 {
          font-weight: bold;
          font-size: 14px;
          margin-bottom: 5px;
		  
		  &.code {
	        font-weight: normal;
	      }
        }

        .order-code {
          color: $color2;
        }

        .order-note {
          font-size: 13px;
          color: $color3;
          font-style: italic;
          line-height: 16px;
          margin-bottom: 5px;
        }
      }

      .order-price {
        display: block;
        width: 100%;
        position: relative;

        .last-price {
          font-size: 24px;
          font-weight: bold;
          color: $sale-color;
          margin-bottom: 5px;
        }

        .active-price {
          font-size: 16px;
          font-weight: bold;
          color: $main-color;
          margin-bottom: 5px;
        }

        .pre-active-price {
          position: relative;
          color: $text-sub-color;

          &:after {
            content: '';
            position: absolute;
            width: 100%;
            height: 1px;
            background: $text-sub-color;
            left: -5%;
            top: 50%;
            @include translate(0, -50%);
          }
        }
      }

      ul.address-list {
        > li {
          display: block;
          position: relative;
          margin-bottom: 10px;
          padding-left: 50px;

          &:last-child {
            margin: 0;
          }

          > i {
            position: absolute;
            left: 10px;
            top: 0;
            display: block;
            vertical-align: top;
          }

          > span {
            word-break: break-word;

            &.user-name {
              font-weight: bold;
            }
          }
        }
      }
    }
  }
}
