#news-article {
  background-color: white;
  border: 1px solid $border-color;
  @include border-radius(5px);
  @include box-shadow(0, 5px, 10px, $border-color);
  .article-header, .article-body {
    padding: 20px 30px;
  }
  .article-header {
    border-bottom: 1px solid $border-color;
  }
  .article-title {
    font-size: 24px;
    @include media-breakpoint-down(lg) {
      font-size: 22px;
    }
    @include media-breakpoint-down(md) {
      font-size: 20px;
    }
    @include media-breakpoint-down(sm) {
      font-size: 18px;
    }
    line-height: 30px;
    font-weight: bold;
    text-transform: uppercase;
  }
  .article-body {
    p, img {
      margin-bottom: 20px;
    }
    h2 {
      font-weight: bold;
      text-transform: uppercase;
      margin-bottom: 10px;
    }
    img {
      display: block;
      width: 100%;
    }
	
	h1, h2, h3, h4, h5, h6 {
		/*font-size: 1.5rem;*/
		margin-bottom: .5rem;
		font-weight: 500;
		line-height: 1.5;
	}
	h2, h3 {
		font-size: 1.5rem;
	}
	
	iframe {
		width: 100%!important;
		margin: 5px auto 20px;
		height: calc(100vw - 145vh)!important;
		min-height: 300px!important;
	}
  }
}