.login-form {
  .modal-body {
    position: relative;
    padding: 0;

    .close {
      position: absolute;
      right: .5rem;
      top: .25rem;
    }

    .login-form-nav {
      border: 0;
      min-height: 50px;
      @include gradient-x(#1c0552, #3c065f);
      @include border-top-radius(4px);
      .popup-title {
        padding: 1.5rem;
        color: white;
        font-weight: 600;
        text-transform: uppercase;
      }
      .nav-item {
        &:first-child {
          position: relative;

          &:after {
            content: '';
            height: 1rem;
            width: 2px;
            background-color: white;
            position: absolute;
            right: 0;
            top: 50%;
            @include translate(0, -50%);
          }
        }
      }

      .nav-link {
        padding: 1.5rem;
        color: white;
        font-weight: 600;
        text-transform: uppercase;
      }
    }

    .tab-content {
      padding: 1rem;

      .form-group {
        margin-bottom: 1.15rem;
      }

      .form-control {
        height: 45px;
        font-size: 14px;
        border-color: $border-color;
        padding-left: 15px;
        box-shadow: none;

        &:hover, &:focus {
          border-color: $main-color;
          box-shadow: none;
        }
      }
    }

    .form-sub-text {
      font-size: .75rem;
      margin: .15rem 0;
      display: block;
      color: $text-color;
    }

    .forgot-password {
      color: $main-color;
      font-size: 12px;
      @include transition(all, .25s);

      &:hover {
        text-decoration: underline;
      }
    }

    .register-form {
      .btn-submit {
        height: 45px;
      }
    }

    .btn-submit {
      display: block;
      width: 100%;
      background-color: #962d91;
      height: 40px;
      margin-bottom: 0;

      &:hover {
        background-color: white;
        border: 2px solid #962d91;
        color: #962d91;
      }
    }

    .btn-facebook {
      display: block;
      width: 100%;
      background-color: #4267b2;
      height: 40px;
      border: 2px solid #4267b2;
      color: white;
      text-transform: uppercase;
      font-size: 12px;
      font-weight: 600;
      padding: 5px 10px;
      @include border-radius(5px);
      @include transition(background-color, 0.3s, ease);

      &:hover {
        background-color: white;
        color: #4267b2;
      }
    }

    .btn-google {
      display: block;
      width: 100%;
      background-color: #e6463e;
      height: 40px;
      border: 2px solid #e6463e;
      color: white;
      text-transform: uppercase;
      font-size: 12px;
      font-weight: 600;
      padding: 5px 10px;
      @include border-radius(5px);
      @include transition(background-color, 0.3s, ease);

      &:hover {
        background-color: white;

        color: #e6463e;
      }
    }
  }
}
