.page-title {
  position: relative;
  font-size: 22px;
  text-transform: uppercase;
  padding: 0 0 0 20px;
  margin-bottom: 10px;
  margin-top: 5px;
  
  @include media-breakpoint-down(md) {
    font-size: 22px;
  }
  @include media-breakpoint-down(sm) {
    font-size: 20px;
  }

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    border-left: 8px solid $main-color;
  }
}

.page-heading {
  font-size: 24px;
  @include media-breakpoint-down(md) {
    font-size: 22px;
  }
  @include media-breakpoint-down(sm) {
    font-size: 20px;
  }
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 20px;
  @include media-breakpoint-down(xs) {
    margin-bottom: 10px;
  }
}

.section-title {
  font-size: 20px;
  text-transform: uppercase;
}

.view-all {
  position: absolute;
  top: 10px;
  right: 15px;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 14px;
  @include media-breakpoint-down(xs) {
    display: none;
  }

  &:hover {
    color: $main-color;
  }
}

.view-all-btn {
	text-align: center;
	color: #bbbbbb;
	font-weight: bold;
	border: 2px solid #bbbbbb;
	border-radius: 2px;
	padding: 10px 0;
	width: 100%;
	margin: 10px auto;
	a {
		color: #aaa;
		text-transform: uppercase;
		@include media-breakpoint-down(xs) {
			text-transform: none;
		}
	}
	@include media-breakpoint-down(xs) {
	  border: 0;
	  padding: 0;
	  margin: 0 0 5px 0;
	  //font-size: 12px;
	  font-weight: normal;
	}
}

.no-message {
  text-align: center;
  p {
    color: #999;
  }
  a {
    display: block;
    color: $main-color;
    margin-top: 10px;
  }
}
