.page-pagination, .section-pagination {
  width: 100%;
  text-align: center;

  .pagination {
    @include justify-content(center);
  }

  .page-item {
    &.active {
      .page-link {
        z-index: 1;
        color: white;
        background-color: $main-color;
      }
    }

    &:first-child {
      .page-link {
        border-left: none;
      }
    }

    &:last-child {
      .page-link {
        border-right: none;
      }
    }

    &:not(:last-child) {
      .page-link {
        border-right: 1px solid $border-color !important;
      }
    }
  }

  .page-link {
    color: $text-color;
    border: none !important;
    padding: 15px 20px;
    margin: 0;
    @include transition(background-color, 0.3s, ease);
    @include border-radius(0 !important);
    @include media-breakpoint-down(sm) {
      padding: 8px 10px;
    }
	@include media-breakpoint-down(xs) {
      padding: 15px 4vw;
    }

    i {
      font-size: 13px;
    }

    &:hover {
      color: white;
      background-color: $main-color;
    }

    &:focus {
      box-shadow: 0 0 0 0.2rem rgba(150, 45, 145, 0.25);
    }
  }
}

.page-pagination {
  background-color: white;
  border: 1px solid $border-color;
  @include border-radius(5px);
  @include box-shadow(0, 5px, 10px, $border-color);
}

.section-pagination {
  border-top: 1px solid $border-color;
}
