#product-order {
  position: relative;
  background-color: white;
  border: 1px solid $border-color;
  box-shadow: 0 0 10px $border-color;
  padding: 20px 30px;
  margin: 20px 0;
  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    width: 5px;
    background-color: $main-color;
  }
  .order-label {
    display: inline-block;
    font-weight: bold;
    margin-right: 50px;
  }
  .order-option {
    display: inline-block;
    .radio {
      display: inline-block;
      margin-right: 50px;
    }
  }
}