.product-list {
  .owl-nav {
    display: block;
    position: absolute;
    left: 50%;
    right: auto;
    top: 40%;
    background: transparent;
    padding: 0;
    width: 106.75%;
    @include transform(translate(0, -50%));
    @include translate(-50%, -50%);
    @include media-breakpoint-down(sm) {
      width: 100%;
    }
  }

  .owl-prev, .owl-next {
    background: transparent !important;
  }

  .owl-prev {
    float: left;
    @include media-breakpoint-up(md) {
      margin-left: -30px;
    }
  }

  .owl-next {
    float: right;
    @include media-breakpoint-up(md) {
      margin-right: -30px;
    }
  }

  i.icon {
    color: $text-color;
    font-size: 30px;
  }

