* {
  outline: none;
}

html {
  width: 100%;
  height: 100%;
}

// Font smoothing hack
html, html a {
  -webkit-font-smoothing: subpixel-antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.004);
}

body {
  background-color: $bg-color;
  color: $text-color;
  font-family: $base-font;
  font-size: $font-size;
  overflow-x: hidden;
  letter-spacing: -0.25px;
  font-style: normal;

  &.darkHeader #BackToTop {
    right: 15px;
  }
}

input, textarea, button, a, p, span {
  font-family: $base-font;
}

input, textarea, button, select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

textarea {
  min-height: 50px;
  resize: vertical;
}

a, a:hover, a:focus, a:active {
  color: $text-color;
  text-decoration: none !important;
  cursor: pointer;
}

p {
  color: $text-color;
  font-size: $font-size;
  line-height: $line-height;
}

strong {
  font-weight: 600;
}

*:focus, :active {
  outline: none !important;
  box-shadow: none;
}

#BackToTop {
  width: 4em;
  height: 4em;
  margin: 0;
  text-indent: -9999em;
  display: block;
  position: fixed;
  right: -100px;
  bottom: 15px;
  z-index: 10;
  background-color: $main-color;
  @include border-radius(50%);
  @include transition(all, .3s);

  img {
    height: 26px;
    position: absolute;
    left: 50%;
    top: 50%;
    z-index: 11;
    @include translate(-50%, -50%);
  }
}
