.form-group {
  margin-bottom: 20px;
}

.form-control {
  height: 45px;
  font-size: 14px;
  border-color: $border-color;
  padding-left: 15px;
  box-shadow: none;
  &:hover, &:focus {
    border-color: $main-color;
    box-shadow: none;
  }
}

.select-group {
  position: relative;
  &:after {
    display: inline-block;
    margin-left: .255em;
    vertical-align: .255em;
    content: "";
    border-top: .3em solid;
    border-right: .3em solid transparent;
    border-bottom: 0;
    border-left: .3em solid transparent;
    position: absolute;
    top: 20px;
    right: 20px;
    pointer-events: none;
  }
}