.header-wrapper {
  width: 100%;
  position: relative;
  z-index: 999;

  .btn-back {
    color: white;
    font-size: 24px;
  }

  .page-contact {
    display: block;
    text-align: right;
    color: white;

    p {
      color: white;
      font-size: 12px;
      margin: 0;
    }

    strong {
      font-size: 24px;
      font-weight: bold;
    }
  }

  .header-top-banner {
    display: block;
    width: 100%;
    position: relative;
    background: white;

    img {
      width: 100%;
      display: block;
    }

    .close-banner{
      position: absolute;
      background-color: transparent;
      box-shadow: none;
      border: none;
      right: 5px;
      top: 5px;
    }

    .container {
      @include media-breakpoint-down(md) {
        max-width: 100%;
        padding: 0;
      }
    }
  }

  .header-user {
    display: block;
    width: 100%;
    position: relative;
    //background-color: #17294d;
    background-color: $bg-color;

    .header-user-list {
      .nav-link {
        color: $text-color;

        span {
          //color: #f0e305;
          color: $sale-color;
          font-weight: bold;
        }

        &.user-action {
          padding-right: 25px;

          i {
            font-size: 14px;
            position: absolute;
            right: 15px;
            top: 8px;
          }
        }
      }
    }
  }

  .header-brand {
    display: block;
    width: 100%;
    position: relative;
    padding: 5px 0;
    background-color: $main-color;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    -webkit-overflow-scrolling: touch;
	z-index: 999;
    @include transition(all, .25s);
    @include media-breakpoint-down(sm) {
      padding: 5px 0;
    }
    @include media-breakpoint-down(md) {
      padding: 5px 0;
    }

    img {
      display: block;
    }

    .menu-toggle {
      @include media-breakpoint-down(xs) {
        top: 10px;
      }
    }

    .header-brand-search {
      display: block;
      width: 100%;
      background-color: white;
      padding: 8px 0;
      @include border-radius(5px);

      @include media-breakpoint-down(xs) {
        padding: 3px 0;
      }

      .header-brand-search-filter {
        display: block;
        width: 100%;
        position: relative;
        border: none;

        &:after {
          position: absolute;
          content: '';
          top: 0;
          right: 0;
          width: 1px;
          background-color: #b7b7b7;
          height: 25px;
        }

        .dropdown-item {
          padding: 10px 35px;

          &:hover {
            background: #efe0ef;
            color: $main-color;
          }
        }

        .header-brand-search-filter-btn {
          display: block;
          width: 100%;
          position: relative;
          border-color: transparent;
          background-color: white;
          text-shadow: none;
          padding: 2px 15px;

          &:active, &:hover, &:focus {
            border-color: transparent;
          }

          &:after {
            right: 10px;
            position: absolute;
            top: 10px;
          }
        }
      }

      .header-brand-search-input {
        display: block;
        width: 100%;
        position: relative;

        &:after {
          position: absolute;
          content: '';
          top: 0;
          right: 0;
          width: 1px;
          height: 25px;
          background-color: #b7b7b7;
        }

        .form-control {
          border: 0;
          font-size: 12px;
          height: 25px;

          &:focus {
            box-shadow: none;
          }
        }
      }

      .header-brand-search-action {
        display: block;
        width: 100%;
        position: relative;
        text-align: center;
      }
    }
  }

  .header-category {
    position: fixed;
    left: 0;
    top: -52px;
    background-color: white;
    padding: 8px 0;
    width: 100%;
    display: none;
    @include transition(all, .25s);
    box-shadow: 0px 3px 10px rgba(#000, 0.15);
	z-index: 98;

    /*.row {
      width: 70vw;
      margin: 0 20%;
    }*/

    &.active {
      top: 52px;
      display: block;

      .container {
        max-width: 60vw;
        @include media-breakpoint-between(sm, lg) {
          max-width: 82vw;
        }

        @include media-breakpoint-between(lg, xl) {
          max-width: 68vw;
        }
      }
    }

    .header-category-item {
      display: block;
      width: 100%;
      vertical-align: top;
      text-align: center;
      position: relative;
      padding-top: 30px;
      @include transition(all, .25s);

      i {
        position: absolute;
        left: 50%;
        top: 32%;
        font-size: 24px;
        display: block;
        color: #333;
        @include transition(all, .25s);
        @include translate(-50%, -50%);
      }

      span {
        line-height: 15px;
        font-size: 13px;
        display: inherit;
        font-weight: 500;
      }

      &.active, &:hover {
        color: $main-color;

        i {
          color: $main-color;
        }
      }
    }
  }

  .logo-brand {
    position: relative;
    display: block;
    @include transition(all, .25s);
    @include media-breakpoint-down(sm) {
      margin-bottom: 10px;
      padding-right: 80px;
    }
    @include media-breakpoint-down(xs) {
      margin-bottom: 5px;
      padding: 0;
      width: 70%;
      height: auto;
    }
  }

  .toggle-category, .user-action-toggle {
    display: none;
    position: absolute;
    right: 20px;
    top: 50%;
    padding: 15px;
    opacity: 0;
    @include translate(0, -50%);
    @include transition(all, .25s);

    i {
      color: white;
    }
  }

  .user-action-toggle {
    @include media-breakpoint-down(sm) {
      opacity: 1;
      font-size: 25px;
      display: block;
      right: -8px;
    }
    @include media-breakpoint-down(xs) {
      right: -8px;
    }
  }

  .header-cart {
    display: inline-block;
    color: white;

    span {
      font-weight: 600;
    }


    i {
      margin-left: 10px;
    }
  }

  .cart-title {
    display: block;
    width: 100%;
    position: relative;
    padding-right: 60px;
    color: white;
    @include transition(all, .25s);
  }

  .user-cart {
    display: none !important;
    width: 100%;
    position: relative;
    padding-right: 60px;
    opacity: 0;
    @include transition(all, .25s);

    span {
      display: block;
      width: 100%;
      position: relative;
      padding-right: 25px;
      color: white;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;

      i {
        position: absolute;
        right: 3px;
        top: -2px;
        font-size: 16px;
        color: white;
      }
    }
  }

  .dropdown-discount {
    display: block;
    padding-left: 30px;
    position: absolute;
    right: 10px;
    top: 0;
    z-index: 1;
    @include media-breakpoint-down(sm) {
      padding-left: 5px;
      top: -12px;
    }

    .header-cart-number {
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      right: auto;
      color: $main-color;
      background-color: #ffff00;
      margin-right: 10px;
      padding: 0 5px;
      @include border-radius(4px);
      @include media-breakpoint-down(sm) {
        width: 10px;
        height: 10px;
        color: transparent;
        left: auto;
        right: -15px;
        @include border-radius(50%);
      }
    }

    .highlight {
      vertical-align: center;

      i {
        color: white;
        @include media-breakpoint-down(sm) {
          font-size: 18px;
        }
      }
    }

    .dropdown-toggle {
      color: white;
    }

    .highlight {
      display: block;
    }

    .dropdown-menu {
      left: auto !important;
      right: 0;
	  top: 25px!important;
	  box-shadow: 0px 5px 15px rgba(0,0,0, 0.15);
	  transform: translate3d(30px, 0, 0)!important;

      li {
        display: block;
        margin: 0;
        min-width: 250px;
        position: relative;
        padding: 0;

        &.dropdown-item {
          padding: 0 !important;

          &:hover, &:focus {
            background-color: white !important;
            color: #333;
            text-decoration: none;
          }
		  em {
		  	padding: 0 30px;
		  	color: #999;
		  }
		  .count {
		  	padding: 0 0 0 5px;
		  	font-weight: lighter;
			color: $text-color;
			font-size: 14px;
		  }
        }

        .product-head {
          display: block;
          width: 300px;
          padding: 10px 15px 10px 85px;
          position: relative;

          .card-image {
            display: block;
            width: 60px;
            padding: 5px;
            height: 45px;
            position: absolute;
            border: 1px solid #ebebeb;
            left: 15px;
            top: 5px;
            @include border-radius(6px);

            img {
              max-height: 80%;
              max-width: 100%;
              width: auto;
              height: auto;
              position: absolute;
              top: 0;
              bottom: 0;
              left: 0;
              right: 0;
              margin: auto;
            }
          }

          .product-head-title {
            overflow: hidden;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            white-space: normal;
            -webkit-box-orient: vertical;
            height: 35px;
            font-size: 14px;

            div, p, span, strong, a {
              text-align: left !important;
              font-weight: 400;
              font-family: inherit !important;
              font-size: inherit !important;
              margin-bottom: 0;
            }
          }

          .product-price {
            display: block;
            width: 100%;
            font-weight: bold;
            color: $sale-color;
          }
        }

        .product-end {
          display: block;
          width: 300px;
          padding: 10px 15px;
          position: relative;
          border-top: 1px solid $border-color;

          .product-end-block {
            display: block;
            width: 100%;
            font-weight: bold;
            position: relative;
            color: $sale-color;
            text-align: right;
            margin-bottom: 10px;

            .product-end-block-price {
              font-weight: bold;
            }

            .product-end-title {
              position: absolute;
              left: 0;
              top: 0;
              font-weight: bold;
              text-transform: uppercase;
              color: $text-color;
            }
          }

          .view-all-product {
            display: block;
            width: 100%;
            padding: 15px;
            text-align: center;
            font-weight: bold;
            text-transform: uppercase;
            color: white;
            background: $sale-color;
            @include border-radius(6px);
          }
        }
      }
    }
  }
}

.header-wrapper-mobile {
  .btn-back {
    color: white;
    font-size: 24px;
  }

  .header-brand-mobile {
    display: block;
    width: 100%;
    position: relative;
    padding: 55px 0;
    background-color: $main-color;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    -webkit-overflow-scrolling: touch;
    @include transition(all, .25s);
    @include media-breakpoint-down(sm) {
      padding: 10px 0;
      position: relative !important;
    }

    .view-cart-title {
      color: white;
      font-size: 18px;
      text-align: center;
      text-transform: uppercase;
    }
  }
}

@import "side-menu";

body {
  -webkit-transition: -webkit-transform .25s ease-in-out;
  transition: -webkit-transform .25s ease-in-out;
  transition: transform .25s ease-in-out;
  transition: transform .25s ease-in-out, -webkit-transform .25s ease-in-out;

  &.darkHeader {
    #Header {
      .header-brand {
        top: 0;
        position: fixed;

        &.wont-fixed {
          top: auto;
          position: relative;
        }
      }

      .logo-brand {
        padding-right: 80px;
        @include media-breakpoint-down(sm) {
          display: none;
        }
      }

      .user-action-toggle {
        display: none;
      }

      .cart-title {
        display: none !important;
        opacity: 0;
      }

      .user-cart {
        display: block !important;
        opacity: 1;
        @include media-breakpoint-down(sm) {
          display: none !important;
        }
      }

      .toggle-category {
        display: block;
        opacity: 1;
      }

      .header-brand-search-wrapper {
        @include media-breakpoint-down(sm) {
          padding-left: 50px;
          @include transition(all, .25s);
        }
      }
    }
  }

  &.clearHeader {
    #Header {
      .header-brand {
        top: auto;
        position: relative;
      }

      .header-category {
        display: none;
      }

      .header-brand-search-wrapper {
        @include media-breakpoint-down(sm) {
          padding-left: 0;
          @include transition(all, .25s);
        }
      }
    }
  }
}

