#profile-personal-info {
  border: 1px solid $border-color;
  @include border-radius(5px);
  margin-bottom: 10px;
  .personal-info-header, .personal-info-body {
    padding: 20px 30px;
  }
  .personal-info-header {
    position: relative;
    border-bottom: 1px solid $border-color;
  }
  .personal-info-body {
    @include media-breakpoint-up(md) {
      min-height: 146px;
    }
  }
  .btn-submit {
    position: absolute;
    top: 50%;
    right: 20px;
    @include transform(translate(0, -50%));
  }
  ul.personal-info-list {
    > li {
      display: block;
      margin-bottom: 10px;
      &:last-child {
        margin: 0;
      }
      > i, > span {
        display: inline-block;
        vertical-align: top;
      }
      > span {
        max-width: 280px;
        margin-left: 20px;
        word-break: break-word;
        &.user-name {
          font-weight: bold;
        }
      }
    }
  }
}