#news-technology-list {
  .news-item {
    position: relative;
    border: 1px solid $border-color;
    overflow: hidden;
    margin-bottom: 10px;
    @include border-radius(5px);
    @include box-shadow(0, 5px, 10px, $border-color);

    &:hover, &:focus {
      @include box-shadow(0, 0, 20px, rgba(0, 0, 0, 0.2));

      .news-image div {
        @include opacity(0.9);
        @include transform(scale3d(1.1, 1.1, 1));
      }
    }

    .news-image {
      position: relative;
      width: 100%;
      overflow: hidden;

      &:before {
        content: "";
        display: block;
        padding-bottom: 72%;
      }

      div {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        text-align: center;
        background: no-repeat scroll center center / cover;
        @include transition(all, 0.5s, ease);
      }
    }

    .news-text {
      background-color: white;
      padding: 15px 20px;
	  height: 100px;

      .news-title {
        /*height: 44px;*/
        line-height: 22px;
        overflow: hidden;
        margin-bottom: 10px;
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
      }

      .news-datetime {
        color: #999;
        font-size: 12px;
        font-style: italic;
      }
    }
  }
}
