#profile-dashboard {
  background-color: white;
  border: 1px solid $border-color;
  @include border-radius(5px);
  box-shadow: 0 0 10px $border-color;
  .dashboard-header {
    position: relative;
    @include horizontal-gradient(#1d0552, #410b74);
    @include border-radii(5px, 5px, 0, 0);
    padding: 20px 30px;
    &:after {
      content: '';
      position: absolute;
      top: -35px;
      right: 20px;
      width: 239px;
      height: 216px;
      background: url("../img/profile/shopping.png") no-repeat center center / 100% auto;
    }
  }
  .dashboard-greeting {
    width: 70%;
    p {
      color: white;
      margin-bottom: 5px;
    }
  }
  .dashboard-body {
    padding: 70px 30px 30px;
  }
}