#profile-change-password, #profile-change-info, #profile-add-address {
  border: 1px solid $border-color;
  @include border-radius(5px);
  margin-bottom: 10px;
  .change-password-header, .change-info-header, .add-address-header {
    position: relative;
    border-bottom: 1px solid $border-color;
    padding: 20px 30px;
  }
  .change-password-body, .change-info-body, .add-address-body {
    padding: 30px;
  }
  .change-password-form, .change-info-form, .add-address-form {
    .form-submit {
      button {
        display: inline-block;
        margin-right: 10px;
      }
    }
    .email-group {
      position: relative;
      > i {
        position: absolute;
        right: 20px;
        top: 13px;
        color: #288925;
      }
    }
  }
}