.product-navigation {
  display: block;
  width: 100%;
  position: relative;
  margin-bottom: 10px;
  @include media-breakpoint-down(xs) {
    margin-bottom: 5px;
  }

  .product-navigation-wrapper {
    display: block;
    position: relative;
    width: 100%;
    @media (min-width: 320px) and (max-width: 1000px) {
      display: none;
    }


    .product-navigation-action {
      position: relative;
      display: block;
      width: 100%;
      padding: 12px 10px 12px 65px;
      text-transform: uppercase;
      font-weight: 700;
      color: white;
      //background-color: #17294d;
      background-color: #999;

      i {
        position: absolute;
        left: 30px;
        font-size: 18px;
        top: 50%;
        @include translate(0, -50%);
      }
    }

    .product-navigation-list {
      position: relative;
      display: block;
      width: 100%;

      li {
        display: block;

        &:last-child {
          a {
            border-bottom: 0;
          }
        }

        a {
          position: relative;
          display: block;
          width: 100%;
          text-transform: uppercase;
          padding: 12px 10px 12px 55px;
          border-bottom: 1px solid #d6d6d6;
          @include transition(all, .25s);
		  
		  @media (min-width: 768px) and (max-width: 1199.98px) {
		    padding: 9px 10px 9px 55px;
		    font-size: 12px;
		  }

          i {
            position: absolute;
            color: #d6d6d6;
            left: 20px;
            font-size: 18px;
            top: 50%;
            @include transition(all, .25s);
            @include translate(0, -50%);
          }

          &:hover, &.active {
            //color: $main-color;
            color: $main-color;
            background: $secondary-color;

            i {
              //color: $main-color;
              color: $main-color;
            }
          }
        }
      }
    }
  }

  .product-navigation-wrapper-mobile {
  	
	.row:not(.no-gutters)>.col, .row:not(.no-gutters)>[class*="col-"] {
		padding: 0 3px;
	}
	
    .product-navigation-block {
      text-align: center;
      display: block;
      padding: 5px 0;
      margin-top: 5px;
      border: 1px solid $main-color;
      @include border-radius(4px);

      i {
        font-size: 32px;
        color: $main-color;
      }

      p {
        /*line-height: 16px;*/
        font-size: 11px;
        color: $main-color;
		/*margin: 10px 0;*/
      }
    }
	
	.container {
		padding: 0 20px;
	}
  }

  .slick-dots {
    position: absolute;
    bottom: 15px;
    left: 0;

    li {
      button {
        width: 8px;
        height: 8px;
        border: 1px solid $main-color;
        @include border-radius(50%);

        &:before {
          display: none;
        }
      }

      &.slick-active {
        button {
          background-color: $main-color;
        }
      }
    }
  }

  .slick-dotted.slick-slider {
    margin: 0;
  }

  .big-banner {
    .item {
      display: block;
      width: 100%;

      img {
        display: block;
        width: 100%;
      }
    }
  }

  .small-banner {
    img {
      display: block;
      width: 100%;
    }
  }
}
