.home-partner-list {
  display: block;
  width: 100%;
  position: relative;
  margin-bottom: 10px;

  .home-partner-list-wrapper {
    .item {
      display: block;
      width: 100%;

      img {
        display: block;
        width: 100%;
        padding: 5px;
      }
    }

    .slick-prev, .slick-next {
      &:before {
        content: '';
      }

      i {
        position: absolute;
        left: 0;
        top: 5px;
        font-size: 25px;
        color: #A8ACB3;
        @include transition(all, .5s);
        @include media-breakpoint-down(md) {
          font-size: 25px;
          opacity: 0.65;
        }
      }

      &:hover {
        i {
          color: #7F8287;
        }
      }

      &.slick-disabled {
        cursor: default;

        &:hover {
          i {
            color: #A8ACB3;
          }
        }
      }
    }

    .slick-prev {
      left: -20px;
      z-index: 1;
      @include media-breakpoint-down(lg) {
        left: -25px;
      }
      @include media-breakpoint-down(md) {
        left: -30px;
      }
      @include media-breakpoint-down(sm) {
        left: 10px;
      }
    }

    .slick-next {
      right: -30px;
      @include media-breakpoint-down(lg) {
        right: 0;
      }
      @include media-breakpoint-down(md) {
        right: -25px;
      }
      @include media-breakpoint-down(sm) {
        right: 0;
      }
    }
  }
}
