.btn-action, .btn-outline-action {
  height: 60px;
  font-size: 16px;
  font-weight: bold;
  text-transform: uppercase;
  border: 2px solid transparent;
}

.btn-action {
  color: white;
  background-color: $main-color;
  border-color: $main-color;

  &:hover {
    color: $main-color;
    background-color: white;
  }

  &:focus {
    @include box-shadow(0, 0, 5px, rgba(150, 45, 145, 0.2))
  }
}

.btn-outline-action {
  color: $main-color;
  background-color: white;
  border-color: $main-color;

  &:hover {
    color: white;
    background-color: $main-color;
  }

  &:focus {
    @include box-shadow(0, 0, 5px, rgba(150, 45, 145, 0.2))
  }
}

.btn-submit {
  position: relative;
  display: inline-block;
  color: white;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  padding: 5px 10px;
  background-color: $main-color;
  border: 1px solid $main-color;
  @include border-radius(5px);
  @include transition(background-color, 0.3s, ease);

  &:hover {
    background-color: white;
    color: $main-color;
  }

  &.form-back {
    padding: 8px 25px 8px 35px;
    font-size: 16px;
    margin-bottom: 15px;
	/*position: fixed;
	bottom: 0;*/

    i {
      position: absolute;
      left: 10px;
      top: 50%;
      @include translate(0, -50%);
    }
  }

  &.form-end{
    padding: 8px 25px 8px 25px;
    font-size: 16px;
    margin-bottom: 15px;
  }

  &.form-checkout {
    padding: 8px 35px 8px 25px;
    font-size: 16px;
    margin-bottom: 15px;
	/*position: fixed;
	bottom: 0;*/

    i {
      position: absolute;
      right: 10px;
      top: 50%;
      @include translate(0, -50%);
    }
  }
}

.btn-cancel {
  display: inline-block;
  background-color: white;
  color: #999;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  border: 1px solid #999;
  @include border-radius(5px);
  padding: 5px 10px;
  @include transition(background-color, 0.3s, ease);

  &:hover {
    background-color: #999;
    color: white;
  }
}

.btn-remove {
  display: inline-block;
  background-color: white;
  color: #e53935;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  border: 1px solid #e53935;
  @include border-radius(5px);
  padding: 5px 10px;
  @include transition(background-color, 0.3s, ease);

  &:hover {
    background-color: #e53935;
    color: white;
  }
}
