#product-filter {
  margin-bottom: 15px;
  @include make-row();

  > div {
	@include media-breakpoint-down(xs) {
	  padding: 0;
	}
	
    @include make-col-ready();

    &:first-child {
      @include media-breakpoint-down(sm) {
        @include make-col(6);
		padding-left: 5px;
      }
    }

    &:last-child {
      @include media-breakpoint-down(sm) {
        @include make-col(6);
		padding-right: 5px;
      }
    }
  }

  .filter-button {
    width: 100%;
    display: block;
    position: relative;
    background-color: white;
    border: 1px solid $border-color;
    box-shadow: 0 0 10px $border-color;
    padding: 15px 20px;
    user-select: none;
    cursor: pointer;

    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      width: 5px;
      background-color: $main-color;
    }

    i {
      position: absolute;
      top: 50%;
      right: 15px;
      transform: translate(0, -50%);
    }
  }
}

#filter-screen, #sort-screen {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 99999;
  width: 100%;
  height: 100vh;
  background-color: rgba(black, .75);

  .filter-screen-wrapper, .sort-screen-wrapper {
    width: 75%;
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    background-color: $bg-color;

    .screen-header {
      position: relative;
      height: 50px;
      background-color: $main-color;
      padding-left: 20px;
      color: white;
      line-height: 50px;
      font-weight: bold;
      text-transform: uppercase;
      text-align: center;

      .back-to-screen {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        width: 50px;

        i {
          color: white;
          font-size: 20px;
        }
      }
    }

    .screen-label {
      font-weight: bold;
      padding: 15px;
      border-bottom: 1px solid $border-color;
    }

    .screen-option {
      cursor: pointer;

      > div {
        padding: 10px 20px;
        border-bottom: 1px solid $border-color;
      }
	  
	  img {
	  	height: 30px!important;
	  }
    }

    .screen-action {
      position: absolute;
      left: 0;
      width: 100%;
      bottom: 0;
      height: 50px;

      .cart-item {
        border-top: 1px solid $border-color;
        @include media-breakpoint-down(sm) {
          padding: 0;
          border: 0;
          border-top: 1px solid $main-color;
        }
      }

      .btn-submit {
        @include media-breakpoint-down(sm) {
          width: 100%;
          height: 50px;
          position: relative;
          padding: 0;
          margin: 0;
          text-align: center;
          vertical-align: middle;
          line-height: 50px;
          border-top: 1px solid $main-color;
          @include border-radius(0);
        }

        &.is-white {
          @include media-breakpoint-down(sm) {
            background-color: white;
            color: $main-color;
          }
        }
      }
    }
  }
}
