#Footer {
  display: block;
  width: 100%;
  position: relative;

  .footer-wrapper {
    position: relative;
    background-color: white;
    border: 1px solid #ebebeb;
    margin-bottom: 10px;
    @include border-radius(5px);
    @include box-shadow(0, 5px, 10px, #ebebeb);

    .border-bottom-footer {
      border-bottom: 1px solid #eeeeee;
    }

    .footer-top {
      display: block;
      width: 100%;
      position: relative;
      padding: 20px;
      @include media-breakpoint-down(sm) {
        padding: 20px 15px 10px
      }

      .footer-block {
        display: block;
        width: 100%;
        position: relative;
        margin-bottom: 5px;
		margin-top: 5px;
        @include media-breakpoint-down(sm) {
          margin-bottom: 10px;
        }

        &.mt-4 {
          @include media-breakpoint-down(sm) {
            margin-top: 0 !important;
          }
        }

        .footer-block-title {
          display: block;
          width: 100%;
          position: relative;
          text-transform: uppercase;
          font-size: 20px;
          font-weight: 300;
          margin-bottom: 10px;
          @include media-breakpoint-down(sm) {
            margin-bottom: 10px;
          }
		  @include media-breakpoint-between(xs, md) {
		  	font-weight: bold;
		  }
        }

        a.footer-block-title {
          &:after {
            position: absolute;
            right: 0;
            top: 35%;
            display: inline-block;
            width: 0;
            height: 0;
            margin-left: .255em;
            vertical-align: .255em;
            content: "";
            border-top: .3em solid;
            border-right: .3em solid transparent;
            border-bottom: 0;
            border-left: .3em solid transparent;
            @include translate(0, -50%);
            @include transition(all, .25s);
          }

          &.collapsed {
            &:after {
              @include rotate(-90);
            }
          }
        }

        .footer-block-list {
          display: block;
          width: 100%;
          position: relative;

          li {
            display: block;
            width: 100%;
            margin-bottom: 5px;

            a {
              color: #333;
              @include transition(all, .25s);

              &:hover {
                color: darken(#333, 25%);
              }

              &.main-color {
                font-weight: bold;
                color: $main-color;
              }
            }
          }
        }

        .footer-block-social {
          display: flex;
          position: relative;
          flex-wrap: wrap;

          li {
            display: flex;
            flex: 0 0 25%;
            align-items: end;
			
			@include media-breakpoint-between(xs, md) {
			  display: flex;
			  flex: 0 0 15%;
			  align-items: end;
			  
			  &:first-child {
			  	display: flex;
			  	flex: 0 0 35%;
			  	align-items: end;
			  	h3 {
			  		margin-top: 10px;
			  	}
			  }
			}

            a {
              font-size: 30px;
              @include transition(all, .25s);

              &:hover {
                color: darken(#333, 25%);
              }
            }
          }
        }
      }

      .footer-contact {
        display: block;
        width: 100%;
        border: 2px solid #eeeeee;
        @include border-radius(5px);
        @include media-breakpoint-down(sm) {
          margin-bottom: 15px;
        }
        @include media-breakpoint-down(xs) {
          display: none;
        }

        .footer-contact-top {
          display: block;
          width: 100%;
          padding: 10px;
          background-color: white;

          .footer-contact-detail {
            display: block;
            width: 100%;
            position: relative;

            .footer-contact-detail-title {
              font-size: 18px;
              font-weight: 700;
              text-transform: uppercase;
              margin-bottom: 15px;
              padding-right: 15px;
            }
          }
        }

        .footer-contact-phone {
          display: block;
          width: 100%;
          padding: 10px 0;
          background-color: #eeeeee;

          a {
            display: block;
            color: $main-color;
            text-transform: uppercase;
            font-weight: bold;
            font-size: 30px;
          }
        }
      }

      .footer-subscriber {
        display: block;
        width: 100%;
        border: 2px solid #eeeeee;
        padding: 10px;
        @include border-radius(5px);
        @include media-breakpoint-down(xs) {
          display: none;
        }

        .footer-subscriber-detail {
          display: block;
          width: 100%;
          position: relative;

          .footer-subscriber-detail-title {
            font-size: 18px;
            font-weight: 700;
            text-transform: uppercase;
            margin-bottom: 15px;
            padding-right: 15px;
          }

          .footer-subscriber-detail-content {
            font-size: 12px;
            line-height: 18px;
            color: $text-sub-color;
            margin-bottom: 15px;
          }

          .form-control {
            color: $text-sub-color;
            height: 40px;
            font-size: 12px;
            border: 1px solid #eeeeee;
          }

          .btn-subscriber {
            background-color: #eeeeee;
            padding: 8px 5px;
            font-weight: 600;
            height: 40px;
            text-transform: uppercase;
            vertical-align: center;
            color: $text-sub-color;
            @include transition(all, .25s);
            @include border-top-right-radius(5px);
            @include border-bottom-right-radius(5px);

            &:hover {
              background-color: darken(#eeeeee, 5%);
            }
          }
        }
      }
    }

    .footer-bottom {
      display: block;
      width: 100%;
      position: relative;
      padding: 20px;
      border-top: 1px solid #ebebeb;
      @include media-breakpoint-down(sm) {
        padding: 20px 15px 10px;
      }

      .image-check {
        text-align: right;
        @include media-breakpoint-down(sm) {
          text-align: left;
        }
      }

      .footer-bottom-detail {
        display: block;
        width: 100%;
        position: relative;

        .footer-bottom-detail-title {
          text-transform: uppercase;
        }

        .footer-bottom-detail-title, .footer-bottom-detail-content {
          font-size: 12px;
          line-height: 18px;
        }
      }
    }
  }
}
