#product-category {
	position: relative;
	background-color: white;
	border: 1px solid $border-color;
	box-shadow: 0 0 10px $border-color;
	margin-bottom: 20px;
	  &:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		width: 100%;
		height: 5px;
		background-color: $main-color;
	}
	
	.category-label {
		font-weight: bold;
	}
	
	.category-header, .category-body {
		padding: 20px 30px;
	}
	
	.category-header {
		border-bottom: 1px solid $border-color;
	}
	
	.category-body {
		.category-label {
			margin-bottom: 15px;
		}
	}
	
	.category-option {
		margin-bottom: 20px;
	    .checkbox {
			margin-bottom: 5px;
			  
			  .checkbox-label {
				&:before {
					/*top: 13px;*/
				}
				
				img {
					height: 40px!important;
			  		/*@include media-breakpoint-down(xs) {
						height: 30px!important;
					}*/
				}
			}
		}
	}
}
