#product-list {
  .product-list-wrapper {
  	@include media-breakpoint-down(xs) {
  	  padding: 0 2px;
  	}
  	
  	.row:not(.no-gutters)>.col, .row:not(.no-gutters)>[class*="col-"] {
  		@include media-breakpoint-down(xs) {
		  padding: 0 5px;
		}
  	}
  }
  
  .product-item {

    &:hover {
      @include box-shadow(0, 0, 20px, rgba(0, 0, 0, 0.2));

      .product-hover {
        display: block;
      }
    }

    .product-name {
      height: 40px;
    }

    .product-hover {
      display: none;
      position: absolute;
      left: 0;
      right: 0;
      bottom: -4px;
      background: white;
      @include border-radii(0, 0, 5px, 5px);
      @include box-shadow(0, 10px, 25px, rgba(0, 0, 0, 0.1));
      overflow: hidden;
      z-index: 3;

      &.no-discount {
        bottom: -5px;
      }

      .product-promotion {
        background-color: #fce9e9;
        border: 2px dotted $sale-color;
        padding: 6px 5px;
        margin: 0 10px 10px 10px;

        > i, > span {
          display: inline-block;
          vertical-align: middle;
        }

        /*> i {
          position: absolute;
          left: 0;
          top: 0;
          width: 12px;
          height: 12px;
          background: url("../img/icon/sale-label.png") no-repeat center center / 100% auto;
        }*/
        > i {
          width: 16px;
          height: 16px;
          background: url("../img/icon/sale-label.png") no-repeat center center / 100% auto;
		  display: none;
        }

        > span {
          color: $sale-color;
          font-size: 11px;
          margin-left: 0px;
		  overflow: hidden;
		  display: -webkit-box;
		  -webkit-line-clamp: 1;
		  -webkit-box-orient: vertical;
        }
      }

      .product-add-to-cart {
        display: none;
        background-color: #e53935;
        text-align: center;
        padding: 8px 0;

        > i, > span {
          color: white;
          display: inline-block;
          vertical-align: middle;
        }

        > i {
          font-size: 20px;
          margin-right: 10px;
        }

        > span {
          font-weight: bold;
          text-transform: uppercase;
        }
      }
    }
  }
}

.product-item {
  position: relative;
  background-color: white;
  border: 1px solid $border-color;
  @include border-radius(5px);
  @include box-shadow(0, 5px, 10px, $border-color);
  padding: 10px;
  margin-bottom: 10px;
  min-height: 330px;
  @include media-breakpoint-down(xs) {
    padding: 5px 10px 0 10px;
  }
  @media (max-width: 320px) {
  	min-height: auto;
  }
  

  &:hover {
    .product-image figure img {
      @include transform(scale(1.1));
    }
  }

  .product-image {
    position: relative;
    width: 100%;

    figure {
      position: relative;
      width: 100%;
      height: 200px;
	  /*@include media-breakpoint-down(xs) {
	    height: ;
	  }*/
	  @media (max-width: 320px) {
	  	height: 140px;
	  }

      img {
        max-height: 100%;
        max-width: 100%;
        width: auto;
        height: auto;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        @include transition(transform, 0.5s, linear);
      }
    }
  }

  .product-text {
    margin-top: 5px;

    .product-name {
      height: 44px;
      line-height: 22px;
      overflow: hidden;
	  display: -webkit-box;
	  -webkit-line-clamp: 2;
	  -webkit-box-orient: vertical;
	  /*height: 33px;*/
      margin-bottom: 5px;
	  @include media-breakpoint-down(xs) {
	    margin-bottom: 0;
		line-height: 20px;
	  }
    }

    .product-price {
      color: $price-color;
      font-size: 22px;
      font-weight: bold;
      margin-bottom: 5px;

      sup {
        font-weight: bold;
      }

      + div {
        height: 30px;
      }

      @include media-breakpoint-down(lg) {
        font-size: 26px;
      }
      @include media-breakpoint-down(md) {
        font-size: 24px;
      }
      @include media-breakpoint-down(sm) {
        font-size: 22px;
      }
    }

    .product-discount {
      max-height: 1px;
      @include media-breakpoint-down(sm) {
        max-height: 100%;
      }

      .percentage-discount, .original-price {
        display: inline-block;
      }

      .percentage-discount {
        color: $sale-color;
        font-weight: bold;
        border: 1px solid $sale-color;
        @include border-radius(2px);
        padding: 4px 12px;
        margin-right: 10px;
      }

      .original-price {
        color: $gray-color;
        text-decoration: line-through;
		@include media-breakpoint-down(xs) {
		  display: none;
		}
      }
    }
  }
}

#home-new-product-wrapper, #recently-viewed-slider {
  .product-item {
    margin-bottom: 10px;
	@include media-breakpoint-down(xs) {
		margin-bottom: 10px;
	}

    .product-hover {
      @include box-shadow(0, 6px, 6px, rgba(0, 0, 0, 0.1));
    }
  }

  .owl-dot.active, .owl-dot:hover {
    span {
      background: $main-color;
    }
  }
}

.error404 {
	text-align: center;
	img {
		width: 50%!important;
	}
}
