.home-new-product {
  display: block;
  width: 100%;
  position: relative;
  margin-bottom: 5px;

  .home-new-product-title-block {
    display: block;
    width: 100%;
    position: relative;
    padding: 15px 15px 10px 35px;
    margin-bottom: 15px;

    &:before {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      width: 4px;
      background-color: $main-color;
    }

    .home-new-product-title {
      font-size: 24px;
      text-transform: uppercase;
      font-weight: 500;
    }

    .home-new-product-more {
      position: absolute;
      right: 0;
      top: 65%;
      text-transform: uppercase;
      @include transition(all, .25s);
      @include translate(0, -50%);

      &:hover {
        right: 15px;
      }
    }
  }

  .product-item {
    &:hover {
      @include box-shadow(0, 0, 20px, rgba(0, 0, 0, 0.2));

      .product-hover {
        display: block;
		@include media-breakpoint-down(xs) {
		  display: none;
		}
      }
    }

    .product-hover {
      display: none;
      position: absolute;
      left: 0;
      right: 0;
      bottom: -8px;
      background: white;
      @include border-radii(0, 0, 5px, 5px);
      @include box-shadow(0, 10px, 25px, rgba(0, 0, 0, 0.1));
      overflow: hidden;
      z-index: 3;
	  
	  &.no-discount {
	  	display: none;
	  }

      .product-promotion {
        background-color: #fce9e9;
        border: 2px dotted $sale-color;
        padding: 6px 5px;
        margin: 0 10px 10px 10px;

        > i, > span {
          display: inline-block;
          vertical-align: middle;
        }

        > i {
          width: 16px;
          height: 16px;
          background: url("../img/icon/sale-label.png") no-repeat center center / 100% auto;
		  display: none;
        }

        > span {
          color: $sale-color;
          font-size: 11px;
          margin-left: 0px;
		  overflow: hidden;
		  display: -webkit-box;
		  -webkit-line-clamp: 1;
		  -webkit-box-orient: vertical;
        }
      }

      .product-add-to-cart {
        display: none;
        background-color: #e53935;
        text-align: center;
        padding: 8px 0;

        > i, > span {
          color: white;
          display: inline-block;
          vertical-align: middle;
        }

        > i {
          font-size: 20px;
          margin-right: 10px;
        }

        > span {
          font-weight: bold;
          text-transform: uppercase;
        }
      }
    }
  }
}
