#profile-welcome {
  background-color: white;
  border: 1px solid $border-color;
  @include border-radius(5px);
  box-shadow: 0 0 10px $border-color;
  padding: 20px 30px;
  margin-bottom: 20px;
  .user-wrapper {
    display: table;
    width: 100%;
    > div {
      display: table-cell;
      vertical-align: middle;
      &:first-child {
        width: 70px;
      }
      &:last-child {
        @include calc(width, '100% - 70px');
        padding-left: 20px;
      }
    }
  }
  .user-avatar {
    width: 70px;
    height: 70px;
    background: no-repeat center center / cover;
    @include border-radius(50%);
    box-shadow: 0 0 10px $border-color;
  }
  .user-name {
    color: $main-color;
    font-weight: bold;
    margin-top: 5px;
	margin-bottom: 5px;
  }
  .logout {
  	color: gray;
  	font-style: italic;
  	font-size: 90%;
  }
}