}

.order-table {
  table {
    th, td {
      &:first-child {
        width: 25%;
        padding-left: 0;
      }
      &:nth-child(2) {
        width: 25%;
      }
      &:nth-child(3) {
        width: 25%;
      }
      /*&:nth-child(4) {
        width: 15%;
      }*/
      &:last-child {
        width: 25%;
        text-align: right;
        padding-right: 0;
      }
    }
    th {
      border: none;
    }
    td {
	  &:first-child {
	  	/*font-style: italic;*/
	  	/*letter-spacing: 0.05em;*/
	  	color: gray;
	  }
      &:nth-child(2) {
	  	color: $main-color;
	  	font-weight: bolder;
	  	a {
		  	color: $main-color;
	  		text-decoration: underline;
	  	}
	  }
      &:nth-child(3) {
        /*color: $main-color;*/
      }
	  
	  &.price {
	  	color: #333;
	  }
	  
	  
    }
  }
}