.cart-info {
  display: block;
  width: 100%;
  position: relative;
  background: white;
  padding: 10px 10px 160px;
  border-right: 1px solid $border-color;
  height: 100vh;
  overflow-y: auto;
  @include box-shadow(0, 0, 5px, $border-color);
  @include media-breakpoint-down(sm) {
    height: 100%;
    padding-bottom: 60px;
    background: transparent;
  }

  .cart-pdf {
    position: fixed;
    left: 0;
    width: 50%;
    bottom: 0;
    background-color: white;
	z-index: 9;

    @include media-breakpoint-down(sm) {
      position: fixed;
      height: 50px;
      width: 100%;
    }
	@include media-breakpoint-down(xs) {
      position: static;
	  background-color: transparent;
    }

    .cart-item {
      border-top: 1px solid $border-color;
      @include media-breakpoint-down(sm) {
        padding: 0;
        border: 0;
        border-top: 1px solid $main-color;
      }
	  .col-8 {
		@include media-breakpoint-down(xs) {
	      display: none;
	    }
	  }
	  .col-4, .col-md-12 {
	  	width: 100%;
	  	max-width: 100%;
	  	flex: 0 0 100%;
	  }
    }

    .btn-submit {
      @include media-breakpoint-down(sm) {
        width: 100%;
        height: 50px;
        position: relative;
        padding: 0;
        margin: 0;
        text-align: center;
        vertical-align: middle;
        line-height: 50px;
        border-top: 1px solid $main-color;
        @include border-radius(0);
      }

      &.is-white {
        @include media-breakpoint-down(sm) {
          background-color: white;
          color: $main-color;
        }
      }
    }
  }

  .cart-item {
    padding: 10px 15px;
    border-bottom: 1px solid $border-color;
	
	&:last-child {
		@include media-breakpoint-down(sm) {
			border-top: 0;
		}
		
		.btn-submit {
			@include media-breakpoint-down(sm) {
				border-radius: 6px;
			}
		}
	}

    .cart-info-title {
      color: $text-color;
      font-size: 20px;
      text-transform: uppercase;
      margin-bottom: 10px;
    }

    .cart-note {
      margin-bottom: 20px;
      text-align: center;
    }

    .cart-info-sub-title {
      color: $text-color;
      font-size: 16px;
      text-transform: uppercase;
    }

    .cart-image {
      display: block;
      width: 100%;
      padding: 5px;
      height: 100px;
      position: relative;
      border: 1px solid $border-color;
      background-color: white;
      @include border-radius(6px);

      @include media-breakpoint-down(sm) {
        height: 95px;
      }

      figure {
        position: relative;
        width: 100%;
        height: 100%;

        img {
          max-height: 100%;
          max-width: 100%;
          width: auto;
          height: auto;
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          margin: auto;
          @include transition(transform, 0.5s, linear);
        }
      }
    }

    .cart-detail {
      display: block;
      width: 100%;
      position: relative;

      h6 {
        font-weight: bold;
        font-size: 14px;
        margin-bottom: 5px;

        span {
          font-weight: normal;

        }
      }

      .cart-code {
        color: $color2;
      }

      .cart-note {
        font-size: 13px;
        color: $color3;
        font-style: italic;
        line-height: 16px;
        margin-bottom: 5px;
      }
    }

    .cart-price {
      display: block;
      width: 100%;
      position: relative;
      margin-bottom: 5px;

      .last-price {
        font-size: 24px;
        font-weight: bold;
        color: $sale-color;
        margin-bottom: 5px;
      }

      .active-price {
        font-size: 16px;
        font-weight: bold;
        color: $main-color;
        margin-bottom: 5px;
      }

      .pre-active-price {
        position: relative;
        color: $text-sub-color;

        &:after {
          content: '';
          position: absolute;
          width: 100%;
          height: 1px;
          background: $text-sub-color;
          left: -5%;
          top: 50%;
          @include translate(0, -50%);
        }
      }
    }

    .cart-action {
      color: $btn-color;
      border: 0;
      font-weight: bold;
      text-transform: uppercase;
      background: transparent;
      position: relative;
      padding-left: 18px;
      font-size: 12px;

      i {
        font-size: 12px;
        position: absolute;
        left: 0;
        top: 0;
      }

      &.btn-remove-item {
        color: #bbb;
        font-weight: normal;
        font-size: 11px;
      }
    }

    .btn-group {
      .btn {
        height: 35px;
        width: 35px;
        padding: 5px;
        background-color: white;
        border-color: $border-color;
      }

      .form-control {
        text-align: center;
        height: 35px;
        padding: 5px;
        width: 45px;
        border-radius: 0;
        border-color: $border-color;

        &:disabled {
          background: white;
        }
      }
    }
  }


}
