#product-info {
  .product-breadcrumb {
    padding: 15px 20px;
    border-bottom: 1px solid $border-color;
  }

  .product-cart {
    padding: 15px 20px 20px;

    .product-name {
      font-size: 22px;
      @include media-breakpoint-down(lg) {
        font-size: 22px;
      }
      @include media-breakpoint-down(md) {
        font-size: 20px;
      }
      @include media-breakpoint-down(sm) {
        font-size: 18px;
      }
      @include media-breakpoint-down(xs) {
        font-size: 24px;
        line-height: 30px;
      }
      margin-bottom: 5px;
    }

    .product-code {
      text-transform: none;
      font-weight: bold;
      margin-bottom: 20px;

      span {
        color: $main-color;
      }
    }

    .product-price {
      color: $sale-color;
      font-size: 30px;
      font-weight: bold;
	  margin-bottom: 10px;

      sup {
        font-weight: bold;
      }
	  
	  .contact {
	  	font-size: 24px;
	  	color: $sale-color;
	  	font-weight: bold;
	  	a {
	  		color: $sale-color;
	  		font-weight: bold;
	  	}
	  }

      @include media-breakpoint-down(lg) {
        font-size: 34px;
      }
      @include media-breakpoint-down(md) {
        font-size: 32px;
      }
      @include media-breakpoint-down(sm) {
        font-size: 30px;
      }
    }

    .product-discount {
      margin: 0 0 20px;

      .percentage-discount, .original-price {
        display: inline-block;
      }

      .percentage-discount {
        background-color: $sale-color;
        color: white;
        font-weight: 600;
        @include border-radius(3px);
        padding: 6px 10px;
        margin-right: 10px;
      }

      .original-price {
        color: $gray-color;
        text-decoration: line-through;
        @include media-breakpoint-down(xs) {
          display: none;
        }
      }
    }

    .product-action {
      @include make-row();

      > div {
        @include make-col-ready();

        &:first-child {
          @include media-breakpoint-up(md) {
            @include make-col(5);
          }
          @include media-breakpoint-down(xs) {
            margin-bottom: 10px;
          }
        }

        &:last-child {
          @include media-breakpoint-up(md) {
            @include make-col(7);
          }
        }
      }

      button {
        width: 100%;
      }
    }
  }

  .product-promotion {
    border-top: 1px solid $border-color;
    padding: 15px 20px;

    .promotion-title {
      margin-bottom: 10px;

      > i, > span {
        display: inline-block;
        vertical-align: middle;
      }

      > i {
        width: 16px;
        height: 16px;
        background: url("../img/icon/black-sale-label.png") no-repeat center center / 100% auto;
		/*display: none;*/
      }

      > span {
        text-transform: uppercase;
        margin-left: 10px;
		/*overflow: hidden;
		display: -webkit-box;
		-webkit-line-clamp: 1;
		-webkit-box-orient: vertical;*/
      }
    }

    .promotion-content {
      height: 100px;
      margin-bottom: 5px;
    }
  }

  .product-link {
    padding: 15px 20px;
    border-top: 1px solid $border-color;

    a {
      color: $main-color;
      text-transform: uppercase;

      > i, > span {
        display: inline-block;
        vertical-align: middle;
      }

      > span {
        text-transform: uppercase;
        margin-right: 10px;
      }
    }
  }
}
