.cart-process {
  display: block;
  width: 100%;
  position: relative;
  

  .process-list {
    display: block;
    width: 100%;
    position: relative;
    padding: 10px 15px 20px 15px;
    background: white;
	@include media-breakpoint-down(xs) {
		display: none;
	}

    .process-list-wrapper {
      display: flex;
      width: 80%;
      margin: 35px auto 0;
      height: 10px;
      position: relative;
      flex-wrap: wrap;
      background: $main-color;
      @include border-radius(15px);

      li {
        position: relative;
        flex: 0 0 33.33%;
        align-items: center;
        text-align: center;

        a {
          width: 30px;
          height: 30px;
          position: absolute;
          top: 50%;
          background: $main-color;
          @include translate(-50%, -50%);
          @include border-radius(50%);
          @include box-shadow(0, 0, 4px, rgba(0, 0, 0, 0.25));
          @include transition(all, .25s);

          &:after {
            content: '';
            width: 15px;
            height: 15px;
            position: absolute;
            left: 50%;
            top: 50%;
            background-color: white;
            opacity: 0.25;
            @include border-radius(50%);
            @include translate(-50%, -50%);
            @include transition(all, .25s);
          }

          &.active, &:hover {
            &:after {
              opacity: 1;
            }
          }

          span {
            display: block;
            position: absolute;
            top: -25px;
            left: 50%;
            text-transform: uppercase;
            font-size: 12px;
            white-space: nowrap;
            @include translate(-50%, 0);
            @include media-breakpoint-down(sm) {
              top: -20px;
              font-size: 10px;
            }
          }
        }

        &:first-child {
          align-items: start;
          text-align: left;

          a {
            left: 0;
            @include translate(0, -50%);
          }
        }

        &:last-child {
          align-items: end;
          text-align: right;

          a {
            right: 0;
            @include translate(0, -50%);
          }
        }
      }
    }
  }

  .process-info {
    display: block;
    width: 100%;
    padding: 5px 15px;

    &-title {
      color: $text-color;
      font-size: 20px;
      text-transform: uppercase;
      margin-bottom: 15px;
    }

    .process-profile {
      display: block;
      width: 100%;
      background-color: white;
      margin-bottom: 10px;
      @include border-radius(4px);
      @include box-shadow(0, 0, 5px, rgba($border-color, .25));

      .process-one-click {
        padding: 35px 0;
        text-align: center;

        &-title {
          color: $text-color;
          font-size: 14px;
          text-transform: uppercase;
          margin-bottom: 20px;
        }

        .process-one-click-list {
          display: block;
          width: 100%;
          text-align: center;

          li {
            display: block;
            margin-bottom: 10px;
            text-align: center;

            &:last-child {
              margin-bottom: 0;
            }

            a {
              display: inline-block;
              min-width: 175px;
              padding: 8px 25px;
              font-weight: bold;
              color: white;
              text-transform: uppercase;
              border: 2px solid transparent;
              @include border-radius(6px);
              @include transition(all, .25s);

              &.process-one-click-facebook {
                background-color: #4267b2;
              }

              &.process-one-click-google {
                background-color: #e6463e;
              }

              &:hover {
                background-color: transparent;

                &.process-one-click-facebook {
                  border: 2px solid #4267b2;
                  color: #4267b2;
                }

                &.process-one-click-google {
                  border: 2px solid #e6463e;
                  color: #e6463e;
                }
              }
            }
          }
        }
      }

      .process-profile-success {
        display: block;
        width: 100%;
        background-color: white;
        @include border-radius(4px);
        @include box-shadow(0, 0, 5px, rgba($border-color, .25));

        .process-profile-success-img {
          padding: 35px;
          text-align: center;
          @include media-breakpoint-down(sm) {
            padding: 10px;
          }

          img {
            margin: 0 auto;
          }
        }

        .process-profile-success-code {
          padding: 35px;
          border-left: 1px solid $border-color;
          @include media-breakpoint-down(sm) {
            padding: 10px;
          }

          .process-profile-success-code-pre {
            font-size: 18px;
            margin-bottom: 15px;
            @include media-breakpoint-down(sm) {
              font-size: 14px;
              margin-bottom: 5px;
            }

            strong {
              font-weight: bold;
            }
          }

          .process-profile-success-code-detail {
            @include media-breakpoint-down(sm) {
              font-size: 12px;
              line-height: 16px;
            }

            a {
              color: $main-color;
            }
          }
        }

        .process-profile-success-about {
          padding: 25px;
          border-top: 1px solid $border-color;

          h6 {
            font-weight: bold;
            margin-bottom: 25px;
          }

          p {
            margin-bottom: 15px;
          }

          &.border-left {
            border-left: 1px solid $border-color;
            @include media-breakpoint-down(sm) {
              border-left: 0;
            }
          }

          &.border-right {
            border-right: 1px solid $border-color;
            @include media-breakpoint-down(sm) {
              border-right: 0;
            }
          }
        }

        .process-profile-success-item {
          display: block;
          position: relative;
          padding: 10px 25px;

          strong {
            font-weight: bold;
          }

          &.border-top {
            border-top: 1px solid $border-color;
          }
        }
      }
    }
  }

  .process-profile-detail {
    display: block;
    position: relative;
    border-left: 1px solid $border-color;

    .process-profile-block-head {
      display: block;
      position: relative;
      padding: 10px 25px;
      border-bottom: 1px solid $border-color;
    }

    .process-profile-block-body {
      display: block;
      position: relative;
      padding: 20px 25px;

      .form-group {
        margin-bottom: 10px;

        &:last-child {
          margin-bottom: 0;
        }
      }

      .process-account {
        display: none;
        position: relative;

        &.active {
          display: block;
        }

        .process-account-detail {
          display: block;
          position: relative;

          &-detail {
            margin-bottom: 10px;
          }

          &-title {
            font-size: 22px;
            font-weight: bold;
            margin-bottom: 10px;
          }
        }
      }

      .process-note {
        display: none;
        position: relative;

        &.active {
          display: block;
        }

        &-title {
          font-size: 13px;
          margin-bottom: 10px;
        }

        &-detail {
          color: $text-sub-color;
        }
      }
    }

    .process-profile-block-sub {
      display: block;
      position: relative;
      padding: 20px 25px;

      .form-group {
        margin-bottom: 10px;

        &.selected-box {
          position: relative;

          &:after {
            display: inline-block;
            position: absolute;
            right: 10px;
            top: 50%;
            margin-left: .255em;
            vertical-align: .255em;
            content: '';
            border-top: .3em solid;
            border-right: .3em solid transparent;
            border-bottom: 0;
            border-left: .3em solid transparent;
            @include translate(0, -50%);
          }
        }

        select.form-control {
          padding-right: 25px;
          position: relative;
        }
      }
    }
  }
  
  .btn-submit {
  	@include media-breakpoint-down(sm) {
  		margin-bottom: 50px;
  		width: 100%;
  	}
  }
  
  .action {
  	@include media-breakpoint-up(md) {
	  position: fixed;
      bottom: 0;
	  z-index: 1;
	  right: 0;
	  width: 50%;
	  background-color: white;
	  border-top: 1px solid $border-color;
    }
	
  	@include media-breakpoint-down(xs) {
      position: static;
	  width: 100%;
    }
	
	.btn-submit {
		margin-bottom: 0;
		@include media-breakpoint-down(xs) {
			width: 100%;
		}
	}
	
	.col-6 {
		@include media-breakpoint-down(xs) {
			width: 100%;
			padding: 0 5px!important;
		}
	}
  }
}
