#profile-info-management {
  background-color: white;
  border: 1px solid $border-color;
  @include border-radius(5px);
  box-shadow: 0 0 10px $border-color;
  margin-bottom: 20px;
  .info-management-header, .info-management-body {
    padding: 20px 30px;
  }
  .info-management-header {
    border-bottom: 1px solid $border-color;
  }
  .info-management-label {
    i, span {
      display: inline-block;
      vertical-align: middle;
    }
    i {
      color: $main-color;
      font-size: 17px;
    }
    span {
      font-size: 16px;
      text-transform: uppercase;
      margin-left: 15px;
    }
  }
  ul.info-management-list {
    > li {
      display: block;
      > a {
        display: block;
        line-height: 26px;
        padding: 5px 0;
        &:hover, &:focus, &.active {
          color: $main-color;
        }
      }
    }
  }
}